import React from "react"

import Layout from '../components/layout'
import SEO from '../components/seo'
import {Link, graphql} from 'gatsby'
import '../styles/courses.scss'
import Img from "gatsby-image";

export default function CoursesPage({data}) {

    const courseImage = data.courseImageQuery.childImageSharp.fixed

    const {lessonsQuery: lessons} = data
    const {coursesQuery: courses} = data

    var courseTimeSeconds = 0;
    var lessonsCount = 0;

    lessons.edges.forEach(lesson => {
        courseTimeSeconds += lesson.node.frontmatter.time;
        ++lessonsCount;
    })

    var minutes = Math.floor(courseTimeSeconds / 60);
    const courseTime = str_pad_left(minutes,'0',2)+'min';

    function str_pad_left(string, pad, length) {
        return (new Array(length+1).join(pad)+string).slice(-length);
    }

    return (
      <Layout>
        <SEO title="Courses" />
        <div className="courses-header pb-5 pt-5">
            <div className="container">
                <h1 className="mb-0">Our Courses</h1>
            </div>
        </div>
        <div className="container mb-5 mt-5">
            {courses.edges.map(course => (
                <div key={course.node.id} className="course pt-4 pb-4 pl-4 pr-4 mb-5">
                    <div className="row">
                        <div className="col-md-4 mb-2">
                            <div className="course-image">
                                <Img fixed={courseImage} height="120" width="120"/>
                            </div>
                        </div>
                        <div className="col-md-8">
                            <div className="price">{course.node.frontmatter.price}</div>
                            <Link key={course.node.id}
                                  className="text-white"
                                  to={course.node.frontmatter.path}
                            ><h2>{course.node.frontmatter.title}</h2></Link>
                            <div className="row">
                                <div className="col-md-3">
                                    <div className="level row ml-0 timing">
                                        <span className="material-icons mr-1">description</span>
                                        <div className="">{lessonsCount} lessons</div>
                                    </div>
                                </div>
                                <div className="col-md-3">
                                    <div className="level row ml-0 timing">
                                        <span className="material-icons mr-1">equalizer</span>
                                        <div className="">{course.node.frontmatter.level}</div>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className="row ml-0 mb-3 timing">
                                        <span className="material-icons mr-1">access_time</span>
                                        <div className="">{courseTime}</div>
                                    </div>
                                </div>
                            </div>
                            <p>{course.node.frontmatter.description}</p>
                        </div>
                    </div>
                </div>
            ))}
        </div>
      </Layout>
    )
}

export const pageQuery = graphql`
  query IndexQuery {
    courseImageQuery: file(relativePath: { eq: "courses/magento-2-for-beginners.png" }) {
        childImageSharp {
          fixed(width: 120) {
             ...GatsbyImageSharpFixed
          }
        }
      }
    coursesQuery: allMarkdownRemark(
      filter: { 
        frontmatter: { 
          type: {eq: "course"}
        }
      }
    ) {
      edges {
        node {
          id
          frontmatter {
            title
            path
            description
            price
            level
          }
        }
      }
    }
    lessonsQuery: allMarkdownRemark (
      filter: {
        frontmatter: {
          type: {
            eq: "lesson"
          }
        }
      }
    ) {
      edges {
        node {
          frontmatter {
            title
            time
          }
        }
      }
    }
  }
`
